import { configureStore } from '@reduxjs/toolkit';
import counterReducer from '../features/counter/counterSlice';
import convoScriptReducer from '../features/convoScript/convoScriptSlice';
import convoScriptReducer2 from '../features/convoScript/convoScriptSlice2';


export const store = configureStore({
  reducer: {
    counter: counterReducer,
    convoScript: convoScriptReducer,
    convoScript2: convoScriptReducer2,
  },
});
