import React, {useState} from 'react'
import { useAuth } from '../contexts/AuthContext';
import {ConvoScript2 as ConvoScript2}  from '../features/convoScript/ConvoScript2'
import {ConvoScript} from '../features/convoScript/ConvoScript'
import { Link, useNavigate } from "react-router-dom";
import {BrowserRouter as Router, Route, Routes} from 'react-router-dom';

import { Button } from 'react-bootstrap'

export default function Dashboard() {

    const [error, setError] = useState('');
    const {user, logout} = useAuth();
    const navigate = useNavigate();
    const [scriptNum, setScriptNum] = useState('1');
    const handleLogout = async () => {
        
        try {
            setError('');
            await logout();
            navigate("/login");
        } catch (error) {
            setError(error.message);
        }
    }

    const handleScript = async () => {
        
        try {
            setError('');
            navigate("/convo2");
        } catch (error) {
            setError(error.message);
        }
    }

    

    return (
        <div>
            
            <strong> logged in as {user.email}</strong>

            <Button className="w-100" variant="primary" onClick={handleLogout}> 
                        Log out
            </Button>

            <Button className="w-100 my-3" variant="primary" onClick={handleScript}> 
                        Script 2
            </Button>

            <ConvoScript/>
            
        </div>


    )
}
