import React, {useRef} from "react";

import {Card, Button, Form, Alert} from "react-bootstrap";
import { Link, useNavigate } from "react-router-dom";

import { useAuth } from "../contexts/AuthContext";

export default function Signup() {

    const emailRef = useRef();
    const passwordRef = useRef();
    const confirmPasswordRef = useRef();

    const [error, setError] = React.useState('');
    const [loading, setLoading] = React.useState(false);

    const navigate = useNavigate();

    const {signUp} = useAuth();

    const handleSubmit = async (e) => {
        e.preventDefault();
        let splitArray = (emailRef.current.value).split('@')
        console.log(splitArray[1] !== 'innovacenteronline.com' && splitArray[1] !== 'brightworld.com')
        if(passwordRef.current.value !== confirmPasswordRef.current.value) {return setError('Passwords do not match');} 
        else if(splitArray[1] !== 'innovacenteronline.com' && splitArray[1] !== 'brightworld.com' ){return setError('Invalid domain extension');}
        else {
            try {
                setError('');
                setLoading(true);
                await signUp(emailRef.current.value, passwordRef.current.value);
                navigate("/");
            } catch(error) {
                setError(error.message);
            }
            setLoading(false);
        }
    };
  return (
    <div>
        <Card> 
            <Card.Body>
                <h2 className="text-center mb-4">Sign up</h2>
                {error && <Alert variant="danger">{error}</Alert>}
                <Form onSubmit={handleSubmit}>
                    <Form.Group controlId="formBasicEmail">
                        <Form.Label>Email address</Form.Label>
                        <Form.Control type="email" placeholder="Enter email" ref={emailRef} required/>
                    </Form.Group>
                    <Form.Group controlId="password">
                        <Form.Label>Password</Form.Label>
                        <Form.Control type="password" placeholder="Enter password" ref={passwordRef} required/>
                    </Form.Group>
                    <Form.Group controlId="password-confirm">
                        <Form.Label>Password Confirmation</Form.Label>
                        <Form.Control type="password" placeholder="Enter password again" ref={confirmPasswordRef} required/>
                    </Form.Group>
                    <Button disabled={loading} className="w-100" variant="primary" type="submit" > 
                        Submit
                    </Button>
                </Form>
            </Card.Body>

        </Card>
        <div className="w-100 d-flex justify-content-center">
          <p>Already have an account? <Link to="/login">Login</Link></p>
        </div>
    </div>
  );
}